import { PolicyManagerSettings } from "./policy-manager.settings";
import { PolicyStore } from "./policy-store";

export class PolicyManager {

    private _settings: PolicyManagerSettings;
    private _defaultSettings: PolicyManagerSettings = {
        authority: null,
        requireHttpsMetadata: true
    };

    private _policyStore = new PolicyStore();

    constructor(settings?: PolicyManagerSettings) {
        this._settings = Object.apply(this._defaultSettings, settings);
    }

    public getPolicy(): Promise<Object> {
        return this._policyStore.getPolicy();
    }
}